<template>
    <div class="content dayActivitiesPage">
        <div v-if="h5Show">
            <H5Header></H5Header>
            <div class="index-bg">
                <p><img src="@/assets/img/new1116/dahuibeijingH5.png" alt=""></p>
                <div>
                    5G2013IMT-20205G5G5G5G2021IMT-2020(5G)5G5G5G 5G
                </div>
                <!-- <Organizational></Organizational> -->
            </div>
            <Footer></Footer>
        </div>
    </div>
</template>
<script>
import H5Header from '@/components/h5Header.vue'
import Header from '@/components/header.vue'
import Footer from '@/components/footer.vue'
import Banner from '@/components/Banner.vue'
// import Organizational from '@/components/Organizational.vue'
export default {
    name:'',
    data(){
        return{
            h5Show:false,
            pcShow:false
        }
    },
    components:{
        H5Header,
        Header,
        Banner,
        // Organizational,
        Footer
    },
    mounted(){
        if (this._isMobile()) {
            this.h5Show = true;
            this.pcShow = false;
        } else {
            this.h5Show = false;
            this.pcShow = true;
        }
        document.title = 'IMT-2020(5G)'
    },
    methods:{
        _isMobile(){
            let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            return flag;
        },
    }

}
</script>

